.footer{
    width: 100%;
    padding: 6rem 0;
    background-color: rgba(19,19,19);
}
.location {
    display: flex;
    flex-direction: row;
    margin-bottom: 0.8rem;

}
.footer-container{
    max-width: 1040px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap:40px;
}
.left{
    height: 100%;
    max-width:100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;

}
.mail{
    display: flex;
    flex-direction: row;
}
.right{
    height: 100%;
    max-width :100%;
    display:flex;
    flex-direction: column;
    padding:1rem;

}
.social{
    margin-top: 1rem;
}
h4{
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}
@media screen and (max-width:640px) {
    .footer-container{
        grid-template-columns: 1fr;
    }
    
}